<template>
  <b-card>
    <template v-if="holidaysList.from">
      <div slot="header" v-if="cardTitle !== ''">
        {{holidaysList.from}} till {{holidaysList.to}}
      </div>
    </template>
    <template v-else>
      No data yet!
    </template>
      <div class="container" v-for="(everyDay, dayIndex) in holidaysList.values" v-bind:key="dayIndex">
        <template v-if="holidaysList.values[dayIndex+1]">
          <div class="row" v-bind:class="(dayIndex === 0) ? 'food-header-zero' : 'food-header'" v-if="everyDay['Date'] === holidaysList.values[dayIndex+1].Date">
            <div class="col">
              <h3>{{everyDay['Date']}}</h3>
              ({{everyDay['Day']}})
            </div>
          </div>
        </template>
        <div class="row food-row text-center">
          <div class="col-sm-12 col-md-4 col-lg-4 ">
              <b-badge style="padding:10px;margin-top:10px;" variant="warning" v-if="everyDay['Option'] === 'Lunch'">{{everyDay['Option']}}</b-badge>
              <b-badge style="padding:10px;margin-top:10px;" variant="success" v-if="everyDay['Option'] === 'Dinner'">{{everyDay['Option']}}</b-badge>
          </div>
          <div class="col-sm-12 col-md-4 col-lg-4 " v-for="(eachHeader, headerIndex) in allFields" :key="headerIndex" v-if="eachHeader !== 'Date' && eachHeader !== 'Day' && eachHeader !== 'Option'">
            <code>{{eachHeader}}</code>
            <div>{{ everyDay[eachHeader] }}</div>
          </div>
        </div>
      </div>
  </b-card>
</template>

<script>
let postsRef = firebase.database().ref('posts');

export default {
  name: 'holidaysList',
  props: {
    cardTitle: String
  },
  data() {
    return {
      holidaysList: [],
      allFields: []
    }
  },
  created() {
    postsRef.on('child_added', snapshot => {
      if( snapshot.val().category === 'holidays-list' ) {
        this.holidaysList = snapshot.val();
        this.allFields = Object.keys(this.holidaysList.values[0]);        
      }
    });
  }
}
</script>

<style>
.food-row {
  margin-bottom: 20px;
}
.food-header {
  padding: 40px 0 30px 0;
  text-align: center;
  border-top: 1px dashed gray;
}
.food-header-zero {
  padding: 10px 0 30px 0;
  text-align: center;
}
</style>



