<template>
  <div class="animated fadeIn disable-select">

    <b-card v-if="selectedName !== 'announcement'">
      <div slot="header">
        <strong> {{ selectedName }} </strong>
        <span v-on:click="resetTemplate()">
          Cancel and go back to choose template
        </span>
      </div>
      <b-row v-if="vmPostType !== 'data'">
        <b-col sm="6" lg="4">
          <b-form-input type="text" placeholder="Category" v-model="categoryName"></b-form-input>
        </b-col>
        <b-col sm="6" lg="4">
          <b-form-input type="text" placeholder="From Date" v-model="fromDate"></b-form-input>
          <b-form-input type="text" placeholder="To Date" v-model="toDate"></b-form-input>
        </b-col>
        <b-col sm="6" lg="4">Checkbox: Dated post</b-col>
      </b-row>
      <table >
        <thead>
          <tr>
            <th v-for="(eachColumn, colIndex) in selectedTemplate.columns" v-bind:key="colIndex">
              {{ eachColumn }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(eachRow, rowIndex) in dataObj" v-bind:key="rowIndex">
            <td v-for="(eachColumn, colIndex) in selectedTemplate.columns" v-bind:key="colIndex">
              <b-form-input type="text" placeholder="" v-model="newRowObj[eachColumn]"></b-form-input>
            </td>
          </tr>
        </tbody>
      </table>
      <b-row >
        <b-col sm="4" lg="4" v-on:click="addNewRow">Add New Row</b-col>
        <b-col sm="4" lg="4" >
          <b-button type="submit" variant="primary" v-on:click="clkSaveChanges">
            <span v-if="vmPostType !== 'data'">Save & Publish</span>
            <span v-if="vmPostType === 'data'">Save Data</span>
          </b-button>
        </b-col>
      </b-row>
    </b-card>
    <b-card v-if="selectedName === 'announcement'">
      TEXT AREA
    </b-card>

  </div>
</template>

<script>

let templatesRef = firebase.database().ref('templates');

import store from '../store';

export default {
  name: 'newRecord',
  data() {
    return {
      selectedTemplate: {},
      selectedKey: 0,
      dataObj: [],
      vmPostType: '',
      newRowObj: {},
      categoryName: '',
      fromDate: '',
      toDate: ''
    }
  },
  computed: {
    selectedName: () => store.state.selectedTemplateName
  },
  methods: {
    initiateControls() {
      this.dataObj = [];
      this.newRowObj = {};
      for (let n=0; n < this.selectedTemplate.columns.length; n++) {
        this.newRowObj[ this.selectedTemplate.columns[n] ] = '';
      }
      this.dataObj.push(this.newRowObj);
    },
    resetTemplate() {
      store.commit('clearSelected');
    },
    addNewRow() {
      this.newRowObj = {};
// BUG: CLICKING MULTIPLE ADD NEW ROW DOESN'T BIND TO THE MODEL
      for (let n=0; n < this.selectedTemplate.columns.length; n++) {
        this.newRowObj[ this.selectedTemplate.columns[n] ] = '';
      }
      this.dataObj.push(this.newRowObj);
    },
    clkSaveChanges() {
      let tmpObj = {
        name: this.selectedName,
        values: JSON.parse(JSON.stringify(this.dataObj)),
        category: this.categoryName,
        from: this.fromDate,
        to: this.toDate
      },
      firebaseRef = "";

// TO DO: CHECK FOR EMPTY/INVALID dataObj VALUES
      if ( this.vmPostType === "data" ) {
        firebaseRef = firebase.database().ref('data');
// TO DO: CHECK TO SEE IF DATA ALREADY PRESENT FOR THIS TEMPLATE
      } else {
        firebaseRef = firebase.database().ref('posts');
// TO DO: CHECK IF THE CATEGORY & DATE RANGE IS SELECTED, AND THEN PUBLISH
// ADD MORE PROPERTY LIKE DATE to tmpObj
      }
console.log(this.dataObj);
      firebaseRef.push(tmpObj, this.fbCallback);
    },
    fbCallback(error) {
      if (error) {
console.log('WRITE FAILED');
      } else {
        // this.initiateControls();
// BUG: RESET DOESN'T REMOVE THE FIRST ROW VALUE
console.log('DATA SAVED SUCCESSFULLY');
      }
    } // FB-CALLBACK
  }, // METHODS
  created: function() {
    templatesRef.on('value', snapshot => {
      for ( let key of Object.keys(snapshot.val()) ) {
        if( snapshot.val()[key].name === this.selectedName ) {
          this.selectedTemplate = snapshot.val()[key];
          this.selectedKey = key;
          this.initiateControls();
          this.vmPostType = this.selectedTemplate.postType;
          break;
        } // IF
      } // FOR
    });
  } // CREATED
}
</script>

<style>
 
</style>



