import Vue from 'vue'
import Router from 'vue-router'

// Containers
import DefaultContainer from '@/containers/DefaultContainer'

// Views
import Dashboard from '@/views/Dashboard'
import Templates from '@/views/Templates'
import CreatePost from '@/views/CreatePost'
import FoodMenu from '@/views/FoodMenu'
import HolidaysList from '@/views/HolidaysList'
import CabSchedule from '@/views/CabSchedule'
import Briefcase from '@/views/Briefcase'

Vue.use(Router)

import store from '../store';

export default new Router({
  mode: 'hash', // App is living in GitHub.io, so required!
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: DefaultContainer,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard
        },
        {
          path: 'templates',
          name: 'Templates',
          component: Templates
        },
        {
          path: 'create-post',
          name: 'Create Post',
          beforeEnter: (to, from, next) => {
            store.commit('clearSelected');
            next()
          },
          component: CreatePost
        },
        {
          path: 'briefcase',
          name: 'Briefcase',
          component: Briefcase
        },
        {
          path: 'food-menu',
          name: 'Food Menu',
          component: FoodMenu
        },
        {
          path: 'cab-schedule',
          name: 'Cab Schedule',
          component: CabSchedule
        },
        {
          path: 'holidays-list',
          name: 'Holidays',
          component: HolidaysList
        }
      ]
    }
  ] // ROUTES
})



